.footer-div {
    min-height: 150px;
    background: #10101f;
    color: #fff;
    position: relative;
    clear: both;
    padding: 20px;
    font-size: 14px;
    line-height: 2em;
    width: 100%;
    bottom: 0;
    display: flex;
    font-weight: 300;
    justify-content: center;
  }
  
.mail.icon::before, 
.github.icon::before,
.linkedin.icon::before {
  color: white;
  height: 80px;
}

.mail-div::before:hover, 
.git-div::before:hover,
.linked-div::before:hover {
  color: rgb(136, 185, 224);
}

.copyright-info {
  margin-left: 30em;
}
 
.contact-panel {
  margin-right: 30em;
  size: 20%;
}