@import url(https://fonts.googleapis.com/css2?family=Lato:wght@300&family=Libre+Baskerville&family=Montserrat:wght@500&family=Raleway:wght@200&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lato:wght@300&family=Libre+Baskerville&family=Montserrat:wght@500&family=Raleway:wght@200&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lato:wght@300&family=Libre+Baskerville&family=Montserrat:wght@500&family=Raleway:wght@200&family=Work+Sans:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@500&family=Raleway:wght@300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.navbar {
    background-color: rgb(157, 192, 238);
    height: 60px;
    display: flex;
    justify-content: start;
    align-items: center;
    position: fixed;
}

.menu-bars {
    margin-left: 1.5rem;
    font-size: 2rem;
    background: none;
}

.nav-menu {
    background-color: rgb(40, 45, 49);
    width: 220px;
    height: 100vh;
    display: flex;
    justify-content: center;
    position: fixed;
    top: 0;
    left: -90%;
    transition: 900ms;
}

.nav-menu.active {
    left: 0;
    transition: 400ms;
}

.nav-text {
    display: flex;
    justify-content: start;
    align-items: center;
    padding: 8px 0px 8px 8px;
    list-style: none;
    height: 60px;
}

.nav-text a {
    text-decoration: none;
    color: #fff;
    font-size: 18px;
    width: 95%;
    height: 100%;
    display: flex;
    align-items: center;
    padding: 0 16px;
    border-radius: 4px;
}

.nav-text a:hover {
    background-color: rgb(243, 221, 217);
}

.nav-menu-items {
    width: 100%;
}

.navbar-toggle {
    background-color: rgb(40, 45, 49);
    height: 60px;
    justify-content: flex-end;
    padding-right: 10px;
    display: flex;
    align-items: center;
}

span {
    margin-left: 8px;
}
.profile-page {
    background-color: rgb(157, 192, 238);
    padding-bottom: 10px;
}

.profile-image {
    height: 220px;
    width: 220px;
    -webkit-filter: sepia(20%);
            filter: sepia(20%);
}

.container-header.main {
    background-color: #fff;
    margin: auto;
    align-items: center;
    justify-content: center;
    display: flex;
    width: 900px;
    border-radius: 9px;
    float: none;
    display: block;
}

.title-name.center.aligned.header {
    padding-top: 70px;
    color: rgb(126, 114, 120);
    font-size: 60px;
    font-weight: 700;
    font-family: 'Lato', sans-serif;
    margin: auto;
    justify-content: center;
    align-items: center;
    padding-bottom: 10px;
}

.about {
    width: 700px;
    justify-content: center;
    padding-top: 20px;
    margin: auto;
    padding-bottom: 20px;
    -webkit-margin-after: auto;
            margin-block-end: auto;
    color: rgb(43, 47, 49);
}

.emoji {
    width: 50px;
}


.technologies {
    width: 100%;
    margin-top: 60px;
    margin-bottom: 50px;
    padding-bottom: 40px;
    
}

.tech-title {
    font-size: 24px;
    font-weight: 300;
    text-align: center;
    padding-top: 5px;
    padding-bottom: 5px;
    border-radius: 30px;
    border-color: rgb(24, 23, 23);
    border-style: solid;
    border-width: 1px;
    font-family: 'Lato', sans-serif;
}

.tech-list {
    padding: 15px;
    margin: auto;
    display: flex;
    justify-content: space-between;
    font-weight: 400px;
}

.html-image {
    height: 60px;
    width: 130px;
}

.node-image {
    height: 80px;
    width: 80px;
}

.react-image {
    height: 90px;
    width: 90px;
}

.mern-image {
    height: 80px;
    width: 180px;
    
}

.tech-images {
    height: 75px;
    width: 75px;
}
.wrapper-projects {
    width: 900px;
    margin: 0 auto 0 auto;
    background-color: rgb(255, 255, 255);
    border-radius: 10px;
    padding: 30px;
}
.container-projects.main {
    margin: 0 auto 0 auto;
    align-items: center;
    justify-content: center;
    display: flex;
    padding-bottom: 60px;
    width: 100%;
    background-color: rgb(157, 192, 238);
}

.title.center.aligned.header {
    color: rgb(126, 114, 120);
    font-size: 50px;
    font-weight: 700;
    font-family: 'Lato', sans-serif;
    padding-top: 30px;
    margin: auto;
    justify-content: center;
    align-items: center;
    padding-bottom: 30px;
}

.projects-list {
    font-size: 14px;
}

.project-titles.header {
    justify-content: center;
    display: flex;
    padding-bottom: 6px;
    font-weight: 200;
    font-size: 18px;
    text-align: center;
    margin: auto;
    font-family: 'Work Sans', sans-serif;
}

.project-description {
    text-align: center;
    font-size: 14px;
    font-weight: 300;
    padding: 10px;
}

.project-image {
    border-style: solid;
    border-color: rgb(43, 42, 42);
    border-width: 1px;
}

.link-buttons {
    padding-top: 10px;
}


.contact-page {
    width: 700px;
    height: 600px;
    justify-content: center;
    margin: auto;
    align-items: center;
    vertical-align: middle;
    margin-top: 9rem;
  
}

.contact-text {
    justify-content: center;
    text-align: center;
    font-family: 'Raleway', sans-serif;
    font-weight: 300;
    margin: auto;
    display: flex;
    color: rgb(44, 43, 43);
    font-size: 15px;
    padding-bottom: 15px;
    padding: 20px;
}

.contact-icons {
    justify-content: center;
    margin: auto;
    display: flex;
}

.icons-contact {
    width: 35px;
    height: 35px;
}

.main-name.center.aligned.header {
    font-family: 'Raleway', sans-serif;
    font-size: 46px;
}
.footer-div {
    min-height: 150px;
    background: #10101f;
    color: #fff;
    position: relative;
    clear: both;
    padding: 20px;
    font-size: 14px;
    line-height: 2em;
    width: 100%;
    bottom: 0;
    display: flex;
    font-weight: 300;
    justify-content: center;
  }
  
.mail.icon::before, 
.github.icon::before,
.linkedin.icon::before {
  color: white;
  height: 80px;
}

.mail-div::before:hover, 
.git-div::before:hover,
.linked-div::before:hover {
  color: rgb(136, 185, 224);
}

.copyright-info {
  margin-left: 30em;
}
 
.contact-panel {
  margin-right: 30em;
  size: 20%;
}
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body::-webkit-scrollbar, .imgrev::before {
  background: #fff;
  
}

body::-webkit-scrollbar-thumb {
  background: rgb(157, 192, 238);
  scroll-behavior: smooth;
  scrollbar-width: 30px;
}

.home, 
.projects, 
.contact {
  display: flex;
  height: 90vh;
  align-items: center;
  justify-content: center;
  font-size: 3rem;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

